.main {
    position: absolute;
    top: 0;
    left: 210px;
    height: 100%;
    width: calc(100% - 210px);
    transition: all 0.5s ease;
    padding: 20px 30px;
}

.sidebar.close ~ .main {
    left: 78px;
    height: 100%;
    width: calc(100% - 78px);
}

.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 210px;
    height: 100%;
    background: #FFF;
    transition: all 0.5s ease;
    padding: 10px 14px;
    z-index: 100;
}

.sidebar.close {
    width: 90px;
}

.sidebar.close .toggle {
    transform: translateY(-50%) rotate(180deg);
}

.sidebar.close .text {
    opacity: 0;
    transition: all 0.3s ease;
}

.toggle-bar {
    position: relative;
}


.menu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: scroll;
}

.menu::-webkit-scrollbar {
    display: none;
}

.menu li .link {
    text-decoration: none;
    height: 50px;
    list-style: none;
    display: flex;
    align-items: center;
    margin: 0 0 10px 0;
}

.menu li .link:hover, .menu li .link.active {
    background: #9dcbff;
    border-radius: 6px;
}

.menu li .link:hover .icon,
.menu li .link:hover .text,
.menu li .link.active .icon,
.menu li .link.active .text {
    color: #FFF;
}

.toggle {
    position: absolute;
    right: -25px;
    top: 10px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    font-size: 22px;
    background: #9dcbff;
    color: #FFF;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
    z-index: 100;
}

.icon {
    min-width: 60px;
    border-radius: 6px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
}

.text {
    font-size: 17px;
    font-weight: 500;
    white-space: nowrap;
    opacity: 1;
}

.text, .icon {
    color: #707070;
    transition: all 0.3s ease;
}

